.header {
  background: white;
}
.logo {
	font-weight: 700;
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 10px;
	margin-left: 10px;
}
.logo span {
	color: #1467C1;
}
.login {
	float: right;
	margin-top: 10px;
	margin-right: 10px;
	@media (max-width: 700px) {
		display: none;
	}
}
.burger, .cross {
	position: relative;
	display: none;
	float: right;
	width: 30px;
	height: 30px;
	border-radius: 0;
	border: none;
	margin-top: 13px;
	margin-right: 10px;
	font-size: 0;
	background: white;
	@media (max-width: 700px) {
		display: block;
	}
}
.burgerLine {
	width: 100%;
    height: 5px;
    position: absolute;
    top: 14px;
    left: 0;
    background: #1467C1;

    &::before,
    &::after {
      content: "";
      width: 100%;
      height: 5px;
      position: absolute;
      left: 0;
      background: #1467C1;
    }

    &::before {
      top: -10px;
    }

    &::after {
      top: 10px;
    }
    &_cross {
      background: transparent;

      &::before,
      &::after {
        content: "";
        top: 0;
        transform-origin: center center;
      }

      &::before {
        transform: rotate(45deg);
      }

      &::after {
        transform: rotate(-45deg);
      }
    }
}
